import React from 'react';
import PropTypes from 'prop-types';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Button from '@material-ui/core/Button';
import { Box } from '@material-ui/core';
import { useSpring, animated } from 'react-spring/web.cjs'; // web.cjs is required for IE 11 support
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import {Contracts, Formatter, Accounts} from '../../../utils/index';

const useStyles = makeStyles(theme => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #000',
    boxShadow: theme.shadows[4],
    padding: theme.spacing(4, 8, 6),
  },
  typeMargin: {
    marginBottom: '15px' 
  },
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    '& > *': {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
      margin: theme.spacing(2),
      width: theme.spacing(35),
      height: theme.spacing(25),
    },
  },
  lpButton: {
    width: 'auto',
    display: 'inline-block',
    marginRight: '12px'
  },
}));

const Fade = React.forwardRef(function Fade(props, ref) {
  const { in: open, children, onEnter, onExited, ...other } = props;
  const style = useSpring({
    from: { opacity: 0 },
    to: { opacity: open ? 1 : 0 },
    onStart: () => {
      if (open && onEnter) {
        onEnter();
      }
    },
    onRest: () => {
      if (!open && onExited) {
        onExited();
      }
    },
  });

  return (
    <animated.div ref={ref} style={style} {...other}>
      {children}
    </animated.div>
  );
});

Fade.propTypes = {
  children: PropTypes.element,
  in: PropTypes.bool.isRequired,
  onEnter: PropTypes.func,
  onExited: PropTypes.func,
};

let addETH = async (manager, pool, ETH) => {
  console.log(manager, pool, ETH.value);
  await Contracts.addPoolETH(manager, pool, ETH.value);
}

export default function SpringModal({manager, pool}) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [syncNum, setSyncNum] = React.useState(0);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const updateAdd = () => {
    setSyncNum(document.getElementById('eth-amount').value);
  }

  return (
    <div className={classes.lpButton}>
      <Button variant="outlined" onClick={handleOpen}>ADD ETH</Button>
      
      <Modal
        aria-labelledby="spring-modal-title"
        aria-describedby="spring-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}>
        <Fade in={open}>
          <div className={classes.paper}>
            <h2 id="spring-modal-title">Add Liquidity</h2>
            <Typography className={classes.typeMargin} variant="body1" gutterBottom>
                You're about to add liquidity to a pool managed by an individual. <br></br>
                Please make sure you know this manager and have a 
                means of communicating with them. <br></br>
                
            </Typography>
            <Divider className={classes.typeMargin} variant="middle" />
            <TextField
                id="eth-amount"
                label="ETH amount"
                type="number"
                InputLabelProps={{
                    shrink: true,
                }}
                variant="outlined"
                className={classes.typeMargin}
                onChange={updateAdd}
            />
            <h3 id="spring-modal-title">Expected Pool Tokens:</h3>
            <Typography className={classes.typeMargin} variant="h1" component="h2" gutterBottom>
                {syncNum} mTokens
            </Typography>
            <Button variant="outlined" onClick={() => addETH(manager, pool, document.getElementById('eth-amount'))}>Add</Button>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
