import React from 'react';
import PropTypes from 'prop-types';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Button from '@material-ui/core/Button';
import { Box } from '@material-ui/core';
import { useSpring, animated } from 'react-spring/web.cjs'; // web.cjs is required for IE 11 support
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import {Contracts, Formatter, Accounts} from '../../../utils/index';
import { useEffect } from 'reactn';

const useStyles = makeStyles(theme => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #000',
    boxShadow: theme.shadows[4],
    padding: theme.spacing(4, 8, 6),
  },
  typeMargin: {
    marginBottom: '15px' 
  },
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    '& > *': {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
      margin: theme.spacing(2),
      width: theme.spacing(35),
      height: theme.spacing(25),
    },
  },
  lpButton: {
      width: 'auto',
      display: 'inline-block',
      marginRight: '12px'
  }
}));



let claimProfit = async (manager, pool) => {
  await Contracts.claimPoolProfit(manager, pool);
    
}

const Fade = React.forwardRef(function Fade(props, ref) {
  const { in: open, children, onEnter, onExited, ...other } = props;
  const style = useSpring({
    from: { opacity: 0 },
    to: { opacity: open ? 1 : 0 },
    onStart: () => {
      if (open && onEnter) {
        onEnter();
      }
    },
    onRest: () => {
      if (!open && onExited) {
        onExited();
      }
    },
  });

  return (
    <animated.div ref={ref} style={style} {...other}>
      {children}
    </animated.div>
  );
});

Fade.propTypes = {
  children: PropTypes.element,
  in: PropTypes.bool.isRequired,
  onEnter: PropTypes.func,
  onExited: PropTypes.func,
};

export default function SpringModal({manager, pool, profit}) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [expectedProfit, setExpectedProfit] = React.useState(0);

  useEffect(() => {
    async function loadData() {
      if (profit > 0) {
        setExpectedProfit(await Contracts.estimatedPoolProfit(manager, pool));
      }
    }       

    loadData();
  }, []);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div className={classes.lpButton}>
      <Button variant="outlined" onClick={handleOpen}>Take Profits</Button>
      
      <Modal
        aria-labelledby="spring-modal-title"
        aria-describedby="spring-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}>
        <Fade in={open}>
          <div className={classes.paper}>
            <h2 id="spring-modal-title">Take ETH Profits</h2>
            <Typography className={classes.typeMargin} variant="body1" gutterBottom>
                Available pool profits can be claimed here. <br></br>
                Claiming operates on FIFO basis and the amount of pool Tokens 
                you have. <br></br>
                
            </Typography>
            <Divider className={classes.typeMargin} variant="middle" />
            <h3 id="spring-modal-title">Available Profit:</h3>
            <Typography className={classes.typeMargin} variant="h1" component="h2" gutterBottom>
                {profit} ETH
            </Typography>
            <h3 id="spring-modal-title">Expected ETH:</h3>
            <Typography className={classes.typeMargin} variant="h1" component="h2" gutterBottom>
                {expectedProfit} ETH
            </Typography>
            
            <Button variant="outlined" onClick={() => claimProfit(manager, pool)}>Claim</Button>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
