import React, { useState } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import { Box } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import { Grid } from '@material-ui/core';
import IntlMessages from '../../../@jumbo/utils/IntlMessages';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import { Redirect, Route, Switch } from 'react-router';
import Typography from '@material-ui/core/Typography';
import AddLiquidity from '../AddLiquidity';
import RemoveLiquidity from '../RemoveLiquidity';
import Profit from '../Profit';
import Restriction from '../Restriction';
import { useLocation } from 'react-router-dom';
import { useEffect } from 'reactn';
import {Contracts, Formatter, Accounts} from '../../../utils/index';

const useStyles = makeStyles(theme => ({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      '& > *': {
        backgroundColor: lighten(theme.palette.background.paper, 0.1),
        // margin: theme.spacing(2),
        // width: theme.spacing(35),
        // height: theme.spacing(25),
      },
    },
    padding: {
        padding: '10px',
        marginTop: '30px',
        width: '100%'
      },
    paddingWarning: {
        padding: '10px',
        marginTop: '5px',
        marginBottom: '15px',
        width: '100%',
        border: '1px solid red'
    },
    tokenPadding: {
        marginBottom: '7px'
    },
    tokenPaddingBottom: {
        marginTop: '15px',
        marginBottom: '5px'
    },
    h1Variant: {
        fontSize: '35px'
    },
    viewPoolContract: {
        marginBottom: '5px',
        
    }
  }));

  


export default function SimplePaper() {
  const classes = useStyles();
  const location = useLocation();
  const [poolInfo, setPoolInfo] = React.useState(null);
  const [manager, setManager] = React.useState(null);
  const [holdings, setHoldings] = React.useState(null);
  const [profits, setProfits] = React.useState(null);
  const [poolAddress, setPoolAddress] = React.useState(0x0000000000000000000000000000000000000000);
  const [poolName, setPoolName]  = React.useState(null);

  let myTokens = 1
  const [myPoolTokens, setMyPoolTokens] = React.useState(0);
  useEffect(() => {
    async function loadData() {
      console.log(location.pathname.split('/'));
      let url = location.pathname.split('/');
      
      setPoolInfo(await Contracts.getSelfPoolData(url[url.length-1]))
      let data = await Contracts.getSelfPoolData(url[url.length-1]);
      setHoldings(data["holdings"]);
      setProfits(data["profits"]);
      setPoolAddress(data["poolToken"]);
      setPoolName(data["name"]);
      setManager(url[url.length-1]);
      console.log(url[url.length-1]);
      console.log(manager);
    }       

    loadData();
  }, []);
  return (
    <PageContainer heading={<IntlMessages id={'Pool name: ' + poolName} />} >
        {poolInfo != null ?
            <div>
                <Box className={classes.root}>
                    <Paper className={classes.paddingWarning} elevation={2}>
                        The Nyan ecosystem allows anyone to create a pool and get access
                        to liquidity. Before interacting with any pool, make sure you know the
                        manager of the pool and trust them. Similar to Etherscan and Uniswap,
                        this site automatically displays stats for all Nyan pools independent 
                        of pool integrity.
                        <br></br>
                        <Button className={classes.tokenPaddingBottom}>View Pool Contract on Etherscan</Button>
                    </Paper>
                </Box>
                <Typography className={classes.tokenPadding} variant="h3" component="h2" gutterBottom>
                    Pool Token: {poolAddress}
                </Typography>
                <Typography className={classes.tokenPaddingBottom} variant="h3" component="h2" gutterBottom>
                    My Tokens: {myTokens}
                </Typography>
                <Box >
                    {manager != null ?
                        <div>
                            <AddLiquidity pool={poolAddress} manager={manager} />
                            <RemoveLiquidity pool={poolAddress} manager={manager} />
                            <Profit pool={poolAddress} manager={manager} profit={profits} />
                            <Restriction />
                        </div>
                        :
                        null
                    }
                
                </Box>
                <Box className={classes.root}>
                    <Grid className={classes.padding} item xs={12} >
                        <Typography variant="h5" component="h5" gutterBottom>
                            TOTAL ETH:
                        </Typography>
                        <Typography className={classes.h1Variant} variant="h1" component="h1" gutterBottom>
                            {holdings}
                        </Typography>
                        <Paper elevation={2}>
                            
                        </Paper>
                    </Grid>
                    <Grid className={classes.padding} item xs={12} >
                        <Typography variant="h5" component="h5" gutterBottom>
                            Available Profits (ETH):
                        </Typography>
                        <Typography className={classes.h1Variant} variant="h1" component="h1" gutterBottom>
                            {profits}
                        </Typography>
                        <Paper elevation={2}>

                        </Paper>
                    </Grid>
                </Box>
            </div>
            :
            null
        }
        
    </PageContainer>
    
  );
}
